import React from 'react';
import PropTypes from 'prop-types';
import { Root, View, Panel , FormLayout, Input, Select, Checkbox, Button, PanelHeader, Group, Header, Cell, InfoRow} from '@vkontakte/vkui';

const Home = ({ id, go}) => (
	<Panel id={id}>
		<PanelHeader>Расчет стоимости пошива</PanelHeader>
		<FormLayout>
            <Group header={<Header mode="secondary">Размер одной шторы</Header>}>
            	<Cell>
            		<div style={{ display: 'flex' }}>
            			<Input id="width" type="number" placeholder="Ширина(см)" onChange={clear}/>
            			<Input id="height" type="number" placeholder="Высота(см)" onChange={clear}/>
            		</div>
            	</Cell>
            </Group>
            
            <Group header={<Header mode="secondary">Параметры пошива</Header>}>
	            <Cell>
	            	<div style={{ display: 'flex' }}>
			            <Select id="count" placeholder="Кол-во штор(шт)" onChange={clear}>
				        	<option value="1">1 шт</option>
				             <option value="2">2 шт</option>
				         </Select>
			            <Select id="band" placeholder="Ширина ленты" onChange={clear}>
				        	<option value="6">6 см</option>
				             <option value="4">4 см</option>
				         </Select>
		         	</div>
		         </Cell>
		         <Cell>
		         	<Checkbox id="isHemmingBottom" onChange={clear}>Подшивать низ</Checkbox>
		         </Cell>
	        </Group>    
            <div style={{ display: 'flex' }}>
            	<Button size="l" onClick={calc}>Рассчитать</Button>
            	<InfoRow header="Стоимость пошива">
		        	<div id="cost"></div>
		        </InfoRow>
        	</div>
          </FormLayout>
	</Panel>
);

const calc = e => {
	var width = Number(document.getElementById("width").value)
	var height = Number(document.getElementById("height").value)
	var count = Number(document.getElementById("count").value)
	var band = document.getElementById("band").value
	var isHemmingBottom = document.getElementById("isHemmingBottom").checked
	
	var cost = (width + height * 2 + (band === "4" ? width : width * 2) + (isHemmingBottom ? width : 0)) * 0.3 * count;
	document.getElementById("cost").innerHTML = cost.toFixed(2) + " ₽";
};

const clear = e => {
	document.getElementById("cost").innerHTML = "";	
}

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired
};

export default Home;
